*{
    border-radius: 0px;
    padding: 0px;
}
.App{
    min-height: 100vh;
    background-color: #000000;
    background-image: url("https://www.transparenttextures.com/patterns/asfalt-light.png");
color: white;
}
.name{
    text-align: center;
    text-shadow: 0 0 2px;
    padding-top: 40px;
    padding-bottom: 18vh;
}

.info{
    display: flex;
    justify-content: center;
    align-content: baseline;
    font-size: larger;
    position: absolute;
    bottom: 10px;
    left: 10px;
}

.view{
    display: flex;
    padding:10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.token,.short,.url{
    font-size: x-large;
    padding:10px;
    position: relative;
}
.token{
    left: 0px;
}
.url{
    left: -0.875rem;
}
.short{
    left:1rem;
}
.token > input , .short>input, .url>input{
    border-width: 0px;
    margin: 10px;
    position: relative;
    padding: 10px;
    background-color: transparent;
    box-shadow: 0 0 2px white ;
    border-radius: 5px;
    color:white;
}
.token > input:focus , .short>input:focus, .url>input:focus {
    outline: none;

}
.token>input{
    left: 40px;
}
.url>input{
    left: 68px;
}
.short > input{
    left:12px;
}

.submitButton{
    background-color: rgba(50, 3, 3,0.7);
    border-width: 0px;
    padding: 8px;
    margin-top: 20px;
    position: relative;
    left:120px;
    border-radius: 5px;
    color:white;
}

a{
    color:royalblue;
    text-decoration: none;
    padding-left: 10px;
}

.result{
    padding-left: 20px;
    color: aqua;
}